import { useState } from "react";
import logo from "./logo.svg";
import "./styles/App.css";

const App = () => {
  const [theme, changeTheme] = useState("light");
  return (
    <div className={`App App-${theme}`}>
      <div className="wrapper">
        <div className={`theme-zone`}>
          <button
            className={`button button-${theme}`}
            onClick={() => changeTheme("light")}
          >
            light
          </button>
          <button
            className={`button button-${theme}`}
            onClick={() => changeTheme("dark")}
          >
            dark
          </button>
        </div>
        <header className={`App-header App-header-${theme}`}>
          <img src={logo} className="logo" alt="logo" />
          <h2>
            Hello <code>ISAG</code>
          </h2>
          <a
            className={`link-github link-github-${theme}`}
            href="https://github.com/ISAG-Lab"
          >
            click here to <span className={`glow-${theme}`}>github</span>
          </a>
        </header>
      </div>
    </div>
  );
};

export default App;
